<template>
  <main>
    <h1>Your Files</h1>
    <p>
      Here you can see all the files you have uploaded and when, where the time
      shown refers to GMC+0. You can also delete one or more files just clicking
      on the red bin button, but be aware: this action <em>can not</em> be
      undone!
    </p>

    <table v-if="uploadedFiles.length !== 0">
      <thead>
        <tr>
          <th>Name</th>
          <th>Size</th>
          <th>Upload Date</th>
          <th>Delete</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="file in uploadedFiles" v-bind:key="file.id">
          <td>{{ file.name }}</td>
          <td>{{ file.size }} byte</td>
          <td>{{ file.uploadDate }}</td>
          <td @click="deleteFile(file.id)" class="delete">
            <img src="delete_button.png" alt="bin" />
          </td>
        </tr>
      </tbody>
    </table>

    <p v-else>
      You haven't load any file yet. Go
      <router-link class="router-link" to="/addfile">here</router-link> to
      upload a file!
    </p>
  </main>
</template>

<script scoped>
import Axios from "axios";

export default {
  name: "Files",
  methods: {
    deleteFile: function (idFile) {
      let rowToDelete = event.currentTarget.parentElement;
      Axios.delete(`${process.env.VUE_APP_APIROOT}uploaderarea/file/${idFile}`)
        .then(() => {
          this.uploadedFiles = this.uploadedFiles.filter(
            (current) => current.id !== idFile
          );
          rowToDelete.remove();
        })
        .catch(() => alert("Non Ã¨ stato possibile eliminare il file"));
    },
  },

  data: function () {
    return {
      uploadedFiles: [],
    };
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/files`)
      .then((response) => (this.uploadedFiles = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

